<template>
  <div>
    <div class="row row-breadcrumb">
      <div class="col-md-6">
        <h4>Listado de Datos</h4>
      </div>
    </div>
    <div class="container">
      <div>
        <div class="row">
          <div class="col-md-6">
            <a-button-group style="margin-bottom: 10px">
              <a-tooltip placement="topLeft" title="Eliminar elementos seleccionados">
                <a-button icon="delete" type="danger" @click="showDeleteConfirm">Eliminar</a-button>
              </a-tooltip>
            </a-button-group>
          </div>
          <div class="col-md-6" style="text-align: end;">
          </div>
        </div>
      </div>
    </div>
    <div class="row">
      <a-modal
          @cancel="onCloseModal"
          :title="selected_datos.get_id()?'Actualizar datos':'Añadir datos'"
          class="modal-form"
          width="55rem"
          :visible="show_modal_form"
          :destroyOnClose="true"
          on-ok="handleOk"
          :header="null"
          :footer="null"
          :maskClosable="false"
      >
        <datos_form :modal=true :model="selected_datos"/>
      </a-modal>
      <datos_table
          :columns="columns"
          class="col-md-8"
          table_name="Datos"
          id_table="id"
          ref="datos_table"
          :params_search="params_search"
          :paginate="paginate"
      />
      <div class="col-md-2">
        <div class="card">
          <div class="card-header">
            <h4>Calculos</h4>
          </div>
          <div class="card-body">
            <p>Promedio:{{ promedio }}</p>
            <p>Cantidad de personas por sexo</p>
            <p>Masculino: {{ masculino }}</p>
            <p>Femenino: {{ femenino }}</p>
            <p>Mayor Edad:{{ mayor_edad }}</p>
            <p>Menor Edad:{{ menor_edad }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import * as utils from "@/helpers/helpers/utils"
import * as mb from "@/helpers/loaders/model.load"
import datos_form from "../form/datos_form"
import datos_table from "./datos.table"

export default {
  name: "datos_list",
  provide: function () {
    return {
      close_modal: this.onCloseModal,
      set_data: this.set_data,
      show_form: this.show_form,
      load_data: () => {
        this.$refs.datos_table.load_data()
      },
      setSelectedDatos: this.setSelectedDatos
    }
  },
  data() {
    return {
      data: [],
      width: document.documentElement.clientWidth,
      height: document.documentElement.clientHeight,
      self: null,
      selected_datos: mb.instance("Datos"),
      show_modal_form: false,
      paginate: false,
      params_search: {relations: []},
      mb
    }
  },
  components: {
    datos_form,
    datos_table
  },
  computed: {
    promedio() {
      let prom = 0
      this.data.forEach(e => {
        prom += e.edad
      })
      return prom
    },
    masculino() {
      let count = 0
      this.data.forEach(e => {
        if (e.sexo === "Masculino")
          count++
      })
      return count
    },
    femenino() {
      let count = 0
      this.data.forEach(e => {
        if (e.sexo === "Femenino")
          count++
      })
      return count;
    },
    columns(){
      return this.width>600?mb.statics("Datos").show_columns([], false):mb.statics("Datos").show_columns(["apellidos","edad"], false)
    },
    mayor_edad() {
      let edad = 0
      let person = {}
      this.data.forEach(e => {
        if (e.edad > edad) {
          edad = e.edad
          person = e
        }
      })
      return person.nombre + " " + person.apellidos
    },
    menor_edad() {
      let edad = 100000
      let person = {}
      this.data.forEach(e => {
        if (e.edad < edad) {
          edad = e.edad
          person = e
        }
      })
      return person.nombre + " " + person.apellidos
    },
  },
  mounted() {
    window.addEventListener('resize', this.getDimensions);
  },
  unmounted() {
    window.removeEventListener('resize', this.getDimensions);
  },
  methods: {
    getDimensions() {
      console.log(document.documentElement.clientWidth)
      this.width = document.documentElement.clientWidth;
      this.height = document.documentElement.clientHeight;
    },
    set_data(data) {
      this.data = data
    },
    setSelectedDatos(model) {
      this.selected_datos = model
    },
    onCloseModal(e, reload_data = false) {
      this.selected_datos = mb.instance("Datos")
      this.show_modal_form = false
      reload_data ? this.$refs.datos_table.load_data() : ""
    },
    show_form() {
      this.show_modal_form = !this.show_modal_form
    },
    showDeleteConfirm() {
      if (this.$refs.datos_table.selectedRowKeys.length === 0) {
        utils.openNotificationWithIcon(
            "error",
            "Eliminar elementos seleccionados",
            "Debe seleccionar al menos un elemento"
        )
        return
      }
      let _this = this
      this.$confirm({
        title: "Eliminar elementos seleccionados?",
        icon: "delete",
        // icon:()=>{return ( <a-icon type="delete" style="color:red"/> )},
        okText: "Si",
        okType: "danger",
        class: "delete_confirm",
        cancelText: "No",
        async onOk() {
          try {
            const response = await mb.statics("Datos").delete_by_ids(
                _this.$refs.datos_table.selectedRowKeys
            )
            utils.process_response(response, "deleted")
            _this.$refs.datos_table.selectedRowKeys = []
            _this.$refs.datos_table.load_data()
          } catch (error) {
            utils.process_error(error)
            _this.$refs.datos_table.selectedRowKeys = []
          }
        },
        onCancel() {
        }
      })
    },
  },
}
</script>

<style>
@import "datos_list.css";
</style>

